	module.exports = {"data":{"post":{"html":"<p>Garret P. Vreeland has some <a href=\"http://www.dangerousmeta.com/?p=10398\">heat beating tips</a> from New Mexico that are applicable to the current German heat wave as well.</p>\n<ol>\n<li>Wear a hat. </li>\n<li>Carry an umbrella. </li>\n<li>Use a hand fan. </li>\n<li>Little bandana things with gel inside.</li>\n<li>Use light moisturizers and sunscreens.</li>\n<li>Loose, light-colored clothing. </li>\n<li>Open footwear. </li>\n<li>Navigate from shade to shade.</li>\n</ol>\n<p>I can only add that it's as much about attitude as anything else. Germans love to piss and moan, but pissing and moaning won't make you feel any cooler. The attitude that you can do something about it will.</p>","fields":{"title":"Beat The Heat","date":"26 Jul 2006","url":"https://papascott.de/2006-07-26-beat-the-heat/"}}},"pathContext":{"slug":"/2006-07-26-beat-the-heat/","prev":{"fields":{"title":"Top 5 Social Media Tools","date":"24 Jul 2006","slug":"/2006-07-24-top-5-social-media-tools/"}},"next":{"fields":{"title":"I'm PapaScott, and you're not","date":"26 Jul 2006","slug":"/2006-07-26-im-papascott-and-youre-not/"}}}}

